* {
    background-color: #101113;
}

.grid {
    position: relative;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    margin: 2%;
}

.node-grid {
    background: transparent;
    position: relative;
}

.nav {
    position: relative;
    display: flex;
    flex-direction: row;
    justify-content:center;
    align-items: center;
    
    margin: 1rem;
    padding: 0.2rem;
    border-radius: 0.4rem;
    background-color: #24203b;
}

.algo-div {
  position: relative;
  display: flex;
  flex-direction: row;
  justify-content:space-between;
  gap: 0.2rem;
  align-items: center;
  margin: 0.2rem;
  padding-left: 0.3rem;
  padding-right: 0.3rem;
  padding-top: 0.05rem;
  padding-bottom: 0.05rem;
  border-radius: 1rem;
  background-color:#1500ff;
  height: 3rem;
}

.algo-div > .algo-button {
  opacity: 0.9;
  background-color: #158f4c;
  width: 8rem;
  height: 2.5rem;
  color: black;
}

.algo-div > .algo-button:hover {
  transform: scale(1.03);
}

.algo-div > .algo-button:active {
  transform: translateY(0.05rem);
}

.button {
  opacity: 0.9;
  margin: 0.3rem;
  font-family:'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
  font-size: 1.3em;
  width: 10rem;
  height: 3rem;
  background-color: #1500ff;
  border-radius: 0.7rem;
  border: none;
  color: whitesmoke;
  font-size: 1.3rem;
}

.button:hover {
    opacity: 1;
    cursor: pointer;
    box-shadow: 0 0.2rem 0.2rem 0rem rgba(0,0,0,0.3);
}

.button:disabled {
  opacity: 0.4;
}

.button:active {
    transform: translateY(0.1rem);
 }

 .slidecontainer {
  display: flex;
  flex-direction:row;
  position: relative;
  background-color: transparent;
  padding: 0.3rem;
  margin: 0.3rem;
  justify-content:center;
  align-items: center;
}

.speed-slider {
  appearance: none;
  -webkit-appearance: none;
  height: 0.7em;
  width: 15em;
  background-color: #3b394a;
  border-radius:2rem;
  margin-left: 1rem;
}

.speed-slider:disabled {
  opacity: 0.4;
}

.speed-slider::-webkit-slider-thumb {
  opacity: 0.9;
  appearance: none;
  -webkit-appearance: none;
  height: 1.5em;
  width: 1.5em;
  border-radius: 50%;
  background-color: #1500ff;
  cursor: pointer;
}

.speed-slider::-webkit-slider-thumb:hover {
  opacity: 1;
}

.slider-text {
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
  color: whitesmoke;
  background-color: transparent;
  font-size: 1.1rem;
}

.w-box {
  opacity: 0.5;
  font-size: 1.1rem;
  font-style: bold;
  font-weight: bolder;
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
  border-radius: 15%;
  background-color: darkorange;
  margin-right: 3rem;
  padding: 0.1rem;

  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;

  width: 2rem;
  height: 2rem;

  box-shadow: 0 0.6rem 1rem 0rem rgba(0,0,0,0.5);
}

.weights-on {
  opacity: 1;
  transform: scale(1.1);
}

.weights-off {
  opacity: 0.5;
}

.top-bar {
  position: relative;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}
